import "semantic-ui-css/semantic.min.css";
import { Menu } from "semantic-ui-react";
import Link from "next/link";

const VerticalMenu = props => (
  <Menu vertical borderless>
    <Menu.Item>
      <Menu.Header>Categories</Menu.Header>
      <Menu.Menu>
        {props.hubs.map(hub => (
          <Link href={`/hub?id=${hub.id}`} as={`/hub/${hub.id}/${hub.slug}`}>
            <Menu.Item name={hub.displayName} />
          </Link>
        ))}
      </Menu.Menu>
    </Menu.Item>
  </Menu>
);

VerticalMenu.getInitialProps = async function() {
  const res = await fetch("http://localhost:3000/api/hubs");
  const data = await res.json();
  const { hubs } = data;
  return {
    hubs
  };
};

export default VerticalMenu;
